.header {
    width: 100%;
    height: 80px;
    margin-bottom: 5%;
    background-color: #585858;
}

.mainMenu a {
    display: inline-block;
    margin: 10px;
    color: #88001b;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
}

.mainMenu a:hover {
    text-decoration: underline;
}